//hero
.hero {
  padding: 350px 0;
  background-image: url("../images/banner.png");
  background-size: cover;
  background-position: center;
  @include rwd($w-pad) {
    h3 {
      font-size: $h4;
    }
    h1 {
      font-size: $h2;
    }
  }
}
//coreValue
.coreValue {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  @include rwd($w-mb) {
    justify-content: center;
  }
  h2 {
    font-size: $h4;
  }
  &__item {
    @include df_center();
    flex-direction: column;
    margin-top: 50px;
    width: 23%;
    div {
      @include size(84px);
      @include df_center();
      img {
        width: 100%;
      }
    }
    h4 {
      text-align: center;
      margin-top: 25px;
      margin-bottom: 8;
    }
    @include rwd($w-pad) {
      width: 48%;
    }
    @include rwd($w-mb) {
      width: 80%;
    }
  }
}
//classic
.classic {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  &__item {
    display: flex;
    &:nth-child(2) {
      align-self: flex-end;
    }
    &_img {
      @include size(635px, 320px);
      background-size: cover;
      background-position: center;
      margin-bottom: 32px;
    }
    &_row {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  @include rwd(1000px) {
    justify-content: center;
    align-items: center;
    &__item {
      width: 100%;
      position: relative;
      &:nth-child(2) {
        align-self: center;
      }
      &_img {
        width: 100%;
      }
      &_row {
        margin: 0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
      }
      * {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }
}
//co-brand
.cobrand {
  background-image: url("../images/bg.png");
  background-size: cover;
  background-position: center;
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    li {
      @include size(540px, 455px);
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      overflow: hidden;
      &:nth-child(1) {
        margin-right: 15px;
        background-image: url("../images/product-8.png");
      }
      &:nth-child(2) {
        background-image: url("../images/product-9.png");
      }
      &:before {
        content: "";
        display: block;
        position: absolute;
        @include size(100vw, 100vh);
        background-color: rgba($white, 0.5);
        left: 0;
        top: 0;
      }
      p {
        position: relative;
        z-index: 2;
      }
    }
    @include rwd(1216px) {
      flex-direction: column;
      li:nth-child(1) {
        margin-right: 0px;
        margin-bottom: 15px;
      }
    }
  }
}
//customerList
.customerList {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  &__item {
    width: 23%;
    box-shadow: 0px 2px 6px #00000029;
    border: 1px solid #dee2e6;
    border-radius: 4px;
    @include rwd($w-pad) {
      width: 100%;
      margin-bottom: 16px;
    }
  }
  &__top {
    @include size(100%, 256px);
    background-size: cover;
    background-position: center;
  }
  &__bottom {
    @include size(100%, 204px);
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}
